.root {
  background: #242424;

}

.inner {
  padding: 32px 20px;
  max-width: 800px;
  margin: 0 auto;
  text-align: center;
  font-size: 20px;
  line-height: 1.3;
  color: #fff;

  a {
    color: #fff;

    &:visited {
      color: #fff;
    }

    &:hover {
      color: darken(#fff, 20%);
    }
  }
}
